<template>
  <v-dialog v-model="dialog" persistent max-width="600px">
    <template v-slot:activator="{ on }">
      <v-btn outlined class="mr-4" color="grey darken-2" v-on="on"
        >Add Event</v-btn
      >
    </template>
    <v-card>
      <v-card-title>
        <span class="headline">Add a New Event</span>
      </v-card-title>
      <v-card-text>
        <v-container>
          <v-row>
            <v-form lazy-validation ref="form" style="width:100%;">
              <v-col cols="12">
                <v-select
                  :items="employees"
                  v-model="eventEmployee"
                  label="Employee*"
                  :rules="rules"
                  filled
                  required
                ></v-select>
              </v-col>
              <v-col cols="12">
                <v-text-field
                  label="Event Title*"
                  v-model="eventTitle"
                  hint="this is what appears in calendar before clicking more"
                  persistent-hint
                  :rules="rules"
                  filled
                  required
                ></v-text-field>
              </v-col>
              <v-col cols="12">
                <v-text-field
                  label="Time Started* YYYY-MM-DD"
                  v-model="eventStart"
                  hint="YYYY-MM-DD"
                  persistent-hint
                  :rules="rules"
                  filled
                  required
                ></v-text-field>
              </v-col>
              <v-col cols="12">
                <v-text-field
                  label="Time Finished* YYYY-MM-DD"
                  v-model="eventEnd"
                  hint="YYYY-MM-DD"
                  persistent-hint
                  :rules="rules"
                  filled
                  required
                ></v-text-field>
              </v-col>
              <v-col cols="12">
                <v-textarea
                  label="Event Details"
                  v-model="eventDetails"
                  rows="1"
                  filled
                  auto-grow
                ></v-textarea>
              </v-col>
              <v-card-actions>
                <small>*indicates required field</small>
                <v-spacer></v-spacer>
                <v-btn color="blue darken-1" text @click="cancel">Close</v-btn>
                <v-btn color="blue darken-1" text @click="addEvent">Save</v-btn>
              </v-card-actions>
            </v-form>
          </v-row>
        </v-container>
      </v-card-text>
    </v-card>
  </v-dialog>
</template>

<script>
import db from "@/components/firebaseInit";

export default {
  name: "AddCalEvent",
  props: ["employees"],
  data() {
    return {
      dialog: false,
      valid: false,
      items: this.employees,
      eventEmployee: null,
      eventTitle: null,
      eventStart: null,
      eventEnd: null,
      eventDetails: null,
      rules: [v => !!v || "This is required"]
    };
  },
  methods: {
    cancel() {
      this.$refs.form.reset();
      this.dialog = false;
    },
    addEvent() {
      this.$refs.form.validate();
      console.log(Date.parse(this.eventStart), Date.parse(this.eventEnd));
      if (
        !this.eventEmployee ||
        !this.eventTitle ||
        !this.eventStart ||
        !this.eventEnd
      ) {
        alert("One of your fields is missing");
        return;
      }
      if (
        this.eventStart.indexOf("/") !== -1 ||
        this.eventEnd.indexOf("/") !== -1 ||
        this.eventStart.indexOf("-") === -1 ||
        this.eventEnd.indexOf("-") === -1
      ) {
        alert("Please use the proper date format");
        return;
      }
      if (Date.parse(this.eventStart) > Date.parse(this.eventEnd)) {
        alert("Your start date is after your end date");
        return;
      }
      db.collection("calevents")
        .add({
          name: this.eventTitle,
          start: this.eventStart,
          end: this.eventEnd,
          details: this.eventDetails
        })
        .then(() => {
          alert("New Event has been added");
          this.dialog = false;
          this.$router.go();
          this.$refs.form.reset();
        })
        .catch(error => alert(error));
    }
  }
};
</script>

